import React from "react";
import { Link } from "react-router-dom";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import { compose } from "redux";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import * as actions from "store/actions";
import { renderInput } from "components/Field";
import Loading from "components/progressbar/Loading";
import validate from "validate.js";

const useStyles = makeStyles(theme => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(3)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },
  links: {
    color: "inherit"
  }
}));

const Signup = props => {
  const classes = useStyles();

  const { handleSubmit } = props;

  const onSubmit = function(formValues) {
    props.signup(formValues, response => {
      if (response.status === 201) {
        props.history.push("/users/activate/account");
      }
    });
  };
  return (
    <Container component="main" maxWidth="xs">
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign up
        </Typography>
        <form
          onSubmit={handleSubmit(onSubmit)}
          className={classes.form}
          noValidate
        >
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Field
                name="userName"
                type="text"
                component={renderInput}
                variant="outlined"
                margin="normal"
                size="small"
                required
                fullWidth
                id="username"
                label="Username:"
                autoComplete="off"
                autoFocus
              />
            </Grid>
            <Grid item xs={12}>
              <Field
                name="email"
                type="text"
                component={renderInput}
                variant="outlined"
                margin="normal"
                size="small"
                required
                fullWidth
                id="email"
                label="Email:"
                autoComplete="off"
              />
            </Grid>
            <Grid item xs={12}>
              <Field
                name="password"
                type="password"
                component={renderInput}
                variant="outlined"
                margin="normal"
                size="small"
                required
                fullWidth
                id="password"
                label="Password:"
                autoComplete="off"
              />
            </Grid>
          </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            disabled={!props.valid}
          >
            {props.loading ? <Loading /> : "Create Account"}
          </Button>
          <Grid container justify="flex-end">
            <Grid item>
              <Link to="/signin" className={classes.links}>
                Already have an account? Sign in
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
    </Container>
  );
};

const schema = {
  userName: {
    presence: { allowEmpty: false, message: "is required" },
    length: {
      maximum: 128
    }
  },
  email: {
    presence: { allowEmpty: false, message: "is required" },
    email: true,
    length: {
      maximum: 128
    }
  },
  password: {
    presence: { allowEmpty: false, message: "is required" },
    length: {
      maximum: 64,
      minimum: 8
    }
  }
};
const validater = formValues => {
  const errors = validate(formValues, schema);
  return errors;
};

function mapStateToProps(state) {
  return {
    loading: state.toggleLoading.toggleLoading
  };
}

export default compose(
  connect(mapStateToProps, actions),
  reduxForm({ form: "signup", validate: validater })
)(Signup);
