import React from "react";
import { Link } from "react-router-dom";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import { compose } from "redux";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import * as actions from "store/actions";
import { renderInput } from "components/Field";
import Loading from "components/progressbar/Loading";
import validate from "validate.js";

const useStyles = makeStyles(theme => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },
  links: {
    color: "inherit"
  }
}));

const Signin = props => {
  const classes = useStyles();
  const { handleSubmit, isAuthenticated } = props;
  React.useEffect(() => {
    if (isAuthenticated) {
      return props.history.push("/dashboard");
    }
  }, [isAuthenticated]);
  const onSubmit = function(formValues) {
    props.signin(formValues, result => {
      if (result.status === 200) {
        props.history.push("/dashboard");
      }
    });
  };
  return (
    <Container component="main" maxWidth="xs">
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <form
          onSubmit={handleSubmit(onSubmit)}
          className={classes.form}
          noValidate
        >
          <Field
            name="username"
            type="text"
            component={renderInput}
            variant="outlined"
            margin="normal"
            size="small"
            required
            fullWidth
            id="username"
            label="Username:"
            autoComplete="off"
            autoFocus
          />
          <Field
            name="password"
            type="password"
            component={renderInput}
            variant="outlined"
            margin="normal"
            size="small"
            required
            fullWidth
            id="password"
            label="Password:"
            autoComplete="off"
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            disabled={!props.valid}
          >
            {props.loading ? <Loading /> : "Login to your account"}
          </Button>
          <Grid container>
            <Grid item xs>
              <Link to="/forgotpassword" className={classes.links}>
                Forgot password?
              </Link>
            </Grid>
            <Grid item>
              <Link to="/signup" className={classes.links}>
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
    </Container>
  );
};
const schema = {
  username: {
    presence: { allowEmpty: false, message: "is required" },
    length: {
      maximum: 128
    }
  },
  password: {
    presence: { allowEmpty: false, message: "is required" },
    length: {
      maximum: 64,
      minimum: 8
    }
  }
};
const validater = formValues => {
  const errors = validate(formValues, schema);
  return errors;
};

function mapStateToProps(state) {
  return {
    isAuthenticated: state.authState.isAuthenticated,
    loading: state.toggleLoading.toggleLoading
  };
}

export default compose(
  connect(mapStateToProps, actions),
  reduxForm({ form: "signin", validate: validater })
)(Signin);
