import React from "react";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import { ProtectedRoute } from "./routers";
import { AdminLayout } from "./layouts";

import DashBoard from "./pages/Dashboard";
import Signin from "./pages/auth/Signin";
import Signup from "./pages/auth/Signup";
import ForgotPassword from "./pages/auth/ForgotPassword";
import ActivateAccount from "./pages/auth/ActivateAccount";
import NotFound from "./pages/auth/404";
import BasicProfile from "./pages/profile/BasicProfile";
import UpdatePhone from "./pages/profile/InputPhone";
import UpdatePassword from "./pages/profile/UpdatePassword";
import UpdateEmail from "./pages/profile/UpdateEmail";
import UpdatePin from "./pages/profile/UpdatePin";

const Routes = () => {
  return (
    <Router>
      <Switch>
        <Route exact path="/signin" component={Signin} />
        <Route exact path="/signup" component={Signup} />
        <Route exact path="/forgotpassword" component={ForgotPassword} />
        <Route
          exact
          path="/users/activate/account"
          component={ActivateAccount}
        />
        <ProtectedRoute
          exact
          path="/dashboard"
          layout={AdminLayout}
          component={DashBoard}
        />
        <ProtectedRoute
          exact
          path="/profile/basic"
          layout={AdminLayout}
          component={BasicProfile}
        />
        <ProtectedRoute
          exact
          path="/profile/updatephone"
          layout={AdminLayout}
          component={UpdatePhone}
        />
        <ProtectedRoute
          exact
          path="/profile/updatepassword"
          layout={AdminLayout}
          component={UpdatePassword}
        />
        <ProtectedRoute
          exact
          path="/profile/updateemail"
          layout={AdminLayout}
          component={UpdateEmail}
        />
        <ProtectedRoute
          exact
          path="/profile/updatepin"
          layout={AdminLayout}
          component={UpdatePin}
        />
        <Route path="/" component={NotFound} />
      </Switch>
    </Router>
  );
};

export default Routes;
