import React, { Component } from "react";
import { withStyles } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import Paper from "@material-ui/core/Paper";
import Toolbar from "@material-ui/core/Toolbar";
import Grid from "@material-ui/core/Grid";
import { Field, reduxForm } from "redux-form";
import { compose } from "redux";
import { connect } from "react-redux";
import * as actions from "store/actions";
import validate from "validate.js";
import Title from "components/Title";
import { renderInput } from "components/Field";
import Loading from "components/progressbar/Loading";

const styles = theme => ({
  textField: {
    width: "100%"
  },
  registerButton: {
    marginTop: theme.spacing(2),
    width: "100%"
  },
  btnGroup: {
    marginRight: theme.spacing(2),
    marginBottom: theme.spacing(2)
  },
  paper: {
    overflow: "hidden",
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2),
    paddingBottom: theme.spacing(2)
  }
});

class UpdatePin extends Component {
  state = {
    sendOTP: false,
    pin: "",
    otp: ""
  };
  onSubmit = formValues => {
    if (!this.state.sendOTP) {
      this.props.updatePin(formValues, result => {
        if (result.status === 204) {
          this.setState({ sendOTP: true });
        }
      });
    } else {
      this.props.verifyPin(formValues, result => {
        if (result.status === 204) {
          this.setState({ sendOTP: false });
        }
      });
    }
  };
  handlePinChange = event => {
    this.setState({ pin: event.target.value });
  };
  handleOTPChange = event => {
    this.setState({ otp: event.target.value });
  };

  render() {
    const { handleSubmit, classes } = this.props;
    if (this.state.sendOTP === false) {
      return (
        <Grid container spacing={3}>
          <Grid item xs={12} md={6} lg={4}>
            <Paper className={classes.paper} elevation={3}>
              <Title>Pin Update</Title>
              <form onSubmit={handleSubmit(this.onSubmit)} noValidate>
                <Field
                  id="pin"
                  name="pin"
                  label="Security Pin"
                  type="password"
                  component={renderInput}
                  required={true}
                  autoComplete="off"
                  className={classes.textField}
                  helperText="This is your four digit security code"
                />
                <Button
                  className={classes.registerButton}
                  size="large"
                  color="primary"
                  variant="contained"
                  type="submit"
                  disabled={!this.props.valid}
                >
                  {this.props.loading ? <Loading /> : "Verify Pin"}
                </Button>
              </form>
            </Paper>
          </Grid>
        </Grid>
      );
    } else {
      return (
        <Grid container spacing={3}>
          <Grid item xs={12} md={6} lg={4}>
            <Paper className={classes.paper} elevation={3}>
              <Toolbar>
                <Title>Verify your Pin</Title>
              </Toolbar>
              <form onSubmit={handleSubmit(this.onSubmit)} noValidate>
                <Field
                  id="verificationCode"
                  name="verificationCode"
                  label="Verification Code"
                  type="number"
                  value={this.state.otp}
                  onChange={this.handleOTPChange}
                  component={renderInput}
                  required={true}
                  autoComplete="off"
                  className={classes.textField}
                />
                <Button
                  onClick={() => this.setState({ sendOTP: false })}
                  size="large"
                  className={classes.btnGroup}
                >
                  Cancel
                </Button>
                <Button
                  size="large"
                  color="primary"
                  variant="contained"
                  type="submit"
                  className={classes.btnGroup}
                >
                  {this.props.loading ? <Loading /> : "Continue"}
                </Button>
              </form>
            </Paper>
          </Grid>
        </Grid>
      );
    }
  }
}
const schema = {
  pin: {
    presence: { allowEmpty: false, message: "is required" },
    length: {
      is: 4
    }
  }
};
const validater = formValues => {
  const errors = validate(formValues, schema);
  return errors;
};

function mapStateToProps(state) {
  return {
    loading: state.toggleLoading.toggleLoading
  };
}

export default compose(
  withStyles(styles),
  connect(mapStateToProps, actions),
  reduxForm({ form: "updateEmail", validate: validater })
)(UpdatePin);
