import React, { Component } from "react";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import { withStyles } from "@material-ui/core";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import { reduxForm } from "redux-form";
import { compose } from "redux";
import { connect } from "react-redux";
import { Field } from "redux-form";
import * as actions from "store/actions";
import { renderInput } from "components/Field";
import validate from "validate.js";
import Title from "components/Title";
import CountrySelect from "components/CountrySelect";
import Loading from "components/progressbar/Loading";

const styles = theme => ({
  form: {},
  fields: {},
  textField: {
    width: "100%"
  },
  registerButton: {
    marginTop: theme.spacing(2),
    width: "100%"
  },
  paper: {
    overflow: "hidden",
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2),
    paddingBottom: theme.spacing(2)
  }
});

class BasicProfile extends Component {
  onSubmit = formValues => {
    this.props.updateUser(formValues, result => {
      this.props.loadUser();
    });
  };
  render() {
    const { handleSubmit, classes, userCountry } = this.props;
    return (
      <Grid container spacing={3}>
        <Grid item xs={12} md={6} lg={4}>
          <Paper className={classes.paper} elevation={3}>
            <Title>Basic Profile</Title>
            <form
              onSubmit={handleSubmit(this.onSubmit)}
              className={classes.form}
              noValidate
            >
              <Field
                id="name"
                name="name"
                label="Name"
                type="text"
                component={renderInput}
                required={true}
                autoComplete="off"
                className={classes.textField}
              />
              <Field
                id="country"
                name="country"
                label="Country"
                type="text"
                component={CountrySelect}
                required={true}
                autoComplete="off"
                className={classes.textField}
                value={userCountry}
              />
              <Field
                id="pin"
                name="pin"
                label="Security Pin"
                type="password"
                component={renderInput}
                required={true}
                autoComplete="off"
                className={classes.textField}
                helperText="This is your four digit security code"
              />
              <Button
                className={classes.registerButton}
                size="large"
                color="primary"
                variant="contained"
                type="submit"
                disabled={!this.props.valid}
              >
                {this.props.loading ? <Loading /> : "Save Profile"}
              </Button>
            </form>
          </Paper>
        </Grid>
      </Grid>
    );
  }
}

const schema = {
  name: {
    presence: { allowEmpty: false, message: "is required" },
    length: {
      maximum: 32
    }
  },
  pin: {
    presence: { allowEmpty: false, message: "is required" },
    length: {
      is: 4
    }
  }
};
const validater = formValues => {
  const errors = validate(formValues, schema);
  return errors;
};

function mapStateToProps(state) {
  return {
    initialValues: state.authState.user,
    userCountry: state.authState.user ? state.authState.user.country : null,
    loading: state.toggleLoading.toggleLoading
  };
}

export default compose(
  withStyles(styles),
  connect(mapStateToProps, actions),
  reduxForm({ form: "basicProfile", validate: validater })
)(BasicProfile);
