import React from "react";
import Box from "@material-ui/core/Box";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import { compose } from "redux";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import * as actions from "store/actions";
import { renderInput } from "components/Field";
import Loading from "components/progressbar/Loading";
import validate from "validate.js";

const useStyles = makeStyles(theme => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  }
}));

const ForgotPassword = props => {
  const classes = useStyles();
  const [switchForm, setSwitchForm] = React.useState(false);
  const { handleSubmit } = props;
  const onSubmit = function(formValues) {
    if (switchForm) {
      props.resetPassword(formValues, result => {
        if (result.status === 200) {
          setSwitchForm(true);
        }
      });
    } else {
      props.verifyPassword(formValues, result => {
        if (result.status === 200) {
          setSwitchForm(false);
          props.history.push("/signin");
        }
      });
    }
  };
  return (
    <Container component="main" maxWidth="xs">
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Reset Password
        </Typography>
        <form
          onSubmit={handleSubmit(onSubmit)}
          className={classes.form}
          noValidate
        >
          <Field
            name="email"
            type="text"
            component={renderInput}
            variant="outlined"
            margin="normal"
            size="small"
            required
            fullWidth
            id="email"
            label="Email:"
            autoComplete="off"
            autoFocus
          />
          <Field
            name="newPassword"
            type="password"
            component={renderInput}
            variant="outlined"
            margin="normal"
            size="small"
            required
            fullWidth
            id="password"
            label="New Password:"
            autoComplete="off"
          />
          <Box display={props.switchForm ? "block" : "none"}>
            <Field
              name="verificationCode"
              type="text"
              component={renderInput}
              variant="outlined"
              margin="normal"
              size="small"
              required
              fullWidth
              id="verificationCode"
              label="OTP:"
              autoComplete="off"
            />
          </Box>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            disabled={!props.valid}
          >
            {props.loading ? <Loading /> : "Reset Password"}
          </Button>
        </form>
      </div>
    </Container>
  );
};

const schema = {
  email: {
    presence: { allowEmpty: false, message: "is required" },
    email: true,
    length: {
      maximum: 128
    }
  },
  newPassword: {
    presence: { allowEmpty: false, message: "is required" },
    length: {
      maximum: 64,
      minimum: 8
    }
  }
};
const validater = formValues => {
  const errors = validate(formValues, schema);
  return errors;
};

function mapStateToProps(state) {
  return {
    loading: state.toggleLoading.toggleLoading
  };
}

export default compose(
  connect(mapStateToProps, actions),
  reduxForm({ form: "forgotPassword", validate: validater })
)(ForgotPassword);
