import React from "react";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";
import { compose } from "redux";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import * as actions from "store/actions";
import { renderInput } from "components/Field";
import Loading from "components/progressbar/Loading";
import Title from "components/Title";
import validate from "validate.js";

const useStyles = makeStyles(theme => ({
  paper: {
    overflow: "hidden",
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2),
    paddingBottom: theme.spacing(2)
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  }
}));

const UpdatePassword = props => {
  const classes = useStyles();
  const { handleSubmit } = props;
  const onSubmit = function(formValues) {
    props.updatePassword(formValues, result => {});
  };

  return (
    <Grid container spacing={3}>
      <Grid item xs={12} md={6} lg={4}>
        <Paper className={classes.paper} elevation={3}>
          <Title>Reset Password</Title>
          <form
            onSubmit={handleSubmit(onSubmit)}
            className={classes.form}
            noValidate
          >
            <Field
              name="oldPassword"
              type="password"
              component={renderInput}
              variant="outlined"
              margin="normal"
              size="small"
              required
              fullWidth
              id="oldpassword"
              label="Current Password:"
              autoComplete="off"
            />
            <Field
              name="newPassword"
              type="password"
              component={renderInput}
              variant="outlined"
              margin="normal"
              size="small"
              required
              fullWidth
              id="newpassword"
              label="New Password:"
              autoComplete="off"
            />
            <Field
              name="confirmPassword"
              type="password"
              component={renderInput}
              variant="outlined"
              margin="normal"
              size="small"
              required
              fullWidth
              id="confirmpassword"
              label="Confirm Password:"
              autoComplete="off"
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              disabled={!props.valid}
            >
              {props.loading ? <Loading /> : "Update Password"}
            </Button>
          </form>
        </Paper>
      </Grid>
    </Grid>
  );
};

const schema = {
  oldPassword: {
    presence: { allowEmpty: false, message: "is required" },
    length: {
      maximum: 64,
      minimum: 8
    }
  },
  newPassword: {
    presence: { allowEmpty: false, message: "is required" },
    equality: {
      attribute: "oldPassword",
      message: "can't be same as old password",
      comparator: function(v1, v2) {
        return !(v1 === v2);
      }
    },
    length: {
      maximum: 64,
      minimum: 8
    }
  },
  confirmPassword: {
    presence: { allowEmpty: false, message: "is required" },
    equality: "newPassword"
  }
};
const validater = formValues => {
  const errors = validate(formValues, schema);
  return errors;
};

function mapStateToProps(state) {
  return {
    loading: state.toggleLoading.toggleLoading
  };
}

export default compose(
  connect(mapStateToProps, actions),
  reduxForm({ form: "updatePassword", validate: validater })
)(UpdatePassword);
