import { api, signinapi } from "api";
import {
  USER_LOADED,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT,
  TOGGLE_LOADING
} from "../types";
import { setUser, getUser, removeUser, isLoggedIn } from "utils/auth";
import { getErrorMessage } from "utils/error";
import { toast } from "react-toastify";

export const signin = (formProps, callback) => async dispatch => {
  dispatch({ type: TOGGLE_LOADING, payload: true });
  const data = JSON.stringify(formProps);
  const user = { userName: formProps.username };
  toast.info("Authentication is in progress");
  try {
    const response = await signinapi.post("", data, {
      params: {
        grant_type: "password",
        scope: "write",
        username: formProps.username,
        password: formProps.password
      }
    });
    if (response.status === 200) {
      toast.success("Authentication Successful");
      setUser(user);
      dispatch({ type: LOGIN_SUCCESS, payload: response.data });
      dispatch({ type: TOGGLE_LOADING, payload: false });
      dispatch(loadUser()); // loadUser must be called only after dispatching LOGIN_SUCCESS
      callback(response);
    }
  } catch (e) {
    const msg = getErrorMessage(e);
    toast.error(msg);
    dispatch({ type: TOGGLE_LOADING, payload: false });
    dispatch({ type: LOGIN_FAIL });
  }
};

export const signup = (formProps, callback) => async dispatch => {
  dispatch({ type: TOGGLE_LOADING, payload: true });
  const data = JSON.stringify(formProps);
  toast.info("Account Creation is in Progress");
  try {
    const response = await api.post("/users/create/", data);
    toast.success("Account Created Successfully");
    dispatch({ type: TOGGLE_LOADING, payload: false });
    callback(response);
  } catch (e) {
    const msg = getErrorMessage(e);
    toast.error(msg);
    dispatch({ type: TOGGLE_LOADING, payload: false });
  }
};
export const resetPassword = (formValues, callback) => async dispatch => {
  dispatch({ type: TOGGLE_LOADING, payload: true });
  toast.info("Reset Password is in progress");
  try {
    const response = await api.post("/users/reset/password/", formValues, {});
    dispatch({ type: TOGGLE_LOADING, payload: false });
    callback(response);
  } catch (e) {
    const msg = getErrorMessage(e);
    toast.error(msg);
    dispatch({ type: TOGGLE_LOADING, payload: false });
  }
};

export const verifyPassword = (formValues, callback) => async dispatch => {
  dispatch({ type: TOGGLE_LOADING, payload: true });
  toast.info("Reset password verification is in progress");
  try {
    const response = await api.post("/users/verify/password/", formValues, {});
    toast.success("New password set Successfully");
    dispatch({ type: TOGGLE_LOADING, payload: false });
    callback(response);
  } catch (e) {
    const msg = getErrorMessage(e);
    toast.error(msg);
    dispatch({ type: TOGGLE_LOADING, payload: false });
  }
};

export const signout = callback => async dispatch => {
  removeUser();
  dispatch({ type: LOGOUT, payload: null });
  toast.info("You have successfully logged out");
  callback();
};

export const loadUser = () => async dispatch => {
  if (!isLoggedIn()) return;
  const token = localStorage.getItem("access_token");
  const userName = await getUser().userName;
  try {
    const response = await api.get(`/users/${userName}/`, {
      params: {
        access_token: token
      }
    });
    const user = response.data;
    user && setUser(user);
    dispatch({ type: USER_LOADED, payload: response.data });
  } catch (e) {
    const msg = getErrorMessage(e);
    toast.error(msg);
  }
};
export const activateAccount = (formProps, callback) => async dispatch => {
  const data = JSON.stringify(formProps);
  try {
    const response = await api.post("/users/activate/", data);
    if (response.status === 200) {
      toast.success("Account Activated Successfully");
    }
    callback();
  } catch (e) {
    const msg = getErrorMessage(e);
    toast.error(msg);
  }
};
