import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";
import { Link } from "react-router-dom";

import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import DashboardIcon from "@material-ui/icons/Dashboard";
import AccountBalanceWalletIcon from "@material-ui/icons/AccountBalanceWallet";
import AttachMoneyIcon from "@material-ui/icons/AttachMoney";
import VerifiedUserIcon from "@material-ui/icons/VerifiedUser";
import SettingsIcon from "@material-ui/icons/Settings";

const useStyles = makeStyles(theme => ({
  root: {
    width: "100%",
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper
  },
  links: {
    textDecoration: "none",
    color: "inherit"
  },
  nested: {
    paddingLeft: theme.spacing(12)
  }
}));

const Sidebar = props => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [selectedIndex, setSelectedIndex] = React.useState(0);

  //Todo - when user navigate to nested link directly from browser url, then handle opening nested itm and select it

  React.useState(() => {
    const path = props.children.props.match.path;
    if (path === "/profile/basic") {
      setOpen(true);
      setSelectedIndex(4);
    } else if (path === "/profile/updatephone") {
      setOpen(true);
      setSelectedIndex(5);
    } else if (path === "/profile/updatepassword") {
      setOpen(true);
      setSelectedIndex(6);
    } else if (path === "/profile/updateemail") {
      setOpen(true);
      setSelectedIndex(7);
    } else if (path === "/profile/updatepin") {
      setOpen(true);
      setSelectedIndex(8);
    }
  });
  const handleClick = () => {
    setOpen(!open);
  };
  const handleListItemClick = (event, index) => {
    setSelectedIndex(index);
  };
  return (
    <List
      component="nav"
      aria-labelledby="nested-list-subheader"
      className={classes.root}
    >
      <ListItem
        button
        selected={selectedIndex === 0}
        onClick={event => handleListItemClick(event, 0)}
      >
        <ListItemIcon>
          <DashboardIcon />
        </ListItemIcon>
        <ListItemText primary="Dashboard" />
      </ListItem>
      <ListItem
        button
        selected={selectedIndex === 1}
        onClick={event => handleListItemClick(event, 1)}
      >
        <ListItemIcon>
          <AccountBalanceWalletIcon />
        </ListItemIcon>
        <ListItemText primary="Wallet" />
      </ListItem>
      <ListItem
        button
        selected={selectedIndex === 2}
        onClick={event => handleListItemClick(event, 2)}
      >
        <ListItemIcon>
          <AttachMoneyIcon />
        </ListItemIcon>
        <ListItemText primary="Trade" />
      </ListItem>
      <ListItem
        button
        selected={selectedIndex === 3}
        onClick={event => handleListItemClick(event, 3)}
      >
        <ListItemIcon>
          <VerifiedUserIcon />
        </ListItemIcon>
        <ListItemText primary="Verification" />
      </ListItem>
      <ListItem button onClick={handleClick}>
        <ListItemIcon>
          <SettingsIcon />
        </ListItemIcon>
        <ListItemText primary="Setting" />
        {open ? <ExpandLess /> : <ExpandMore />}
      </ListItem>
      <Collapse in={open} timeout="auto" unmountOnExit>
        <List component="div" dense>
          <ListItem
            button
            selected={selectedIndex === 4}
            onClick={event => handleListItemClick(event, 4)}
            className={classes.nested}
          >
            <Link to="/profile/basic" className={classes.links}>
              <ListItemText primary="Basic Profile" />
            </Link>
          </ListItem>
          <ListItem
            button
            selected={selectedIndex === 5}
            onClick={event => handleListItemClick(event, 5)}
            className={classes.nested}
          >
            <Link to="/profile/updatephone" className={classes.links}>
              <ListItemText primary="Update Phone" />
            </Link>
          </ListItem>
          <ListItem
            button
            selected={selectedIndex === 7}
            onClick={event => handleListItemClick(event, 7)}
            className={classes.nested}
          >
            <Link to="/profile/updateemail" className={classes.links}>
              <ListItemText primary="Update Email" />
            </Link>
          </ListItem>
          <ListItem
            button
            selected={selectedIndex === 8}
            onClick={event => handleListItemClick(event, 8)}
            className={classes.nested}
          >
            <Link to="/profile/updatepin" className={classes.links}>
              <ListItemText primary="Reset Pin" />
            </Link>
          </ListItem>
          <ListItem
            button
            selected={selectedIndex === 6}
            onClick={event => handleListItemClick(event, 6)}
            className={classes.nested}
          >
            <Link to="/profile/updatepassword" className={classes.links}>
              <ListItemText primary="Reset Password" />
            </Link>
          </ListItem>
        </List>
      </Collapse>
    </List>
  );
};

export default Sidebar;
