import React from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";

import { compose } from "redux";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import * as actions from "store/actions";

import { renderInput } from "components/Field";
import Loading from "components/progressbar/Loading";

import validate from "validate.js";

const useStyles = makeStyles(theme => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  }
}));

const ActivateAccount = props => {
  const classes = useStyles();
  const { handleSubmit } = props;
  const onSubmit = function(formValues) {
    props.activateAccount(formValues, response => {
      props.history.push("/signin");
    });
  };
  return (
    <Container component="main" maxWidth="xs">
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Please Activate your account
        </Typography>
        <form
          onSubmit={handleSubmit(onSubmit)}
          className={classes.form}
          noValidate
        >
          <Field
            name="userName"
            type="text"
            component={renderInput}
            variant="outlined"
            margin="normal"
            size="small"
            required
            fullWidth
            id="username"
            label="Username:"
            autoComplete="off"
            autoFocus
          />
          <Field
            name="activationCode"
            type="text"
            component={renderInput}
            variant="outlined"
            margin="normal"
            size="small"
            required
            fullWidth
            id="activationcode"
            label="Activation Code:"
            autoComplete="off"
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            disabled={!props.valid}
          >
            {props.loading ? <Loading /> : "Activate your account"}
          </Button>
        </form>
      </div>
    </Container>
  );
};
const schema = {
  userName: {
    presence: { allowEmpty: false, message: "is required" },
    length: {
      maximum: 128
    }
  },
  activationCode: {
    presence: { allowEmpty: false, message: "is required" }
  }
};
const validater = formValues => {
  const errors = validate(formValues, schema);
  return errors;
};

function mapStateToProps(state) {
  return {
    loading: state.toggleLoading.toggleLoading
  };
}

export default compose(
  connect(mapStateToProps, actions),
  reduxForm({ form: "activateaccount", validate: validater })
)(ActivateAccount);
